<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <!-- 数据表格 -->
    <BasicTable
      class="fix-basic-table"
      @register="registerTable"
      :searchInfo="searchInfo"
      @edit-end="handleEditEnd"
      @selection-change="handleSelectChange"
      @resize-column="(w, col) => (col.width = w)"
    >
      <template #toolbar>
        <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleAdd">添加</a-button>
        <a-button
          type="primary"
          :disabled="selectedRowId == ''"
          :preIcon="IconEnum.EDIT"
          @click="handleEdit"
          >修改</a-button
        >

        <a-button
          class="red-btn"
          :disabled="selectedRowId == ''"
          :preIcon="IconEnum.DELETE"
          @click="handleDel"
          >删除</a-button
        >

        <a-button
          type="primary"
          :disabled="selectedRowId == ''"
          :preIcon="IconEnum.ADD"
          @click="handleConfig"
          >评分规则</a-button
        >
        <a-button type="primary" :preIcon="IconEnum.ADD" @click="saveSorts">保存排序号</a-button>
        <a-button
          class="green-btn"
          :disabled="selectedRowId == ''"
          :preIcon="IconEnum.ENABLE"
          @click="updateIndexStatus(1)"
          >启用</a-button
        >
        <a-button
          class="grey-btn"
          :disabled="selectedRowId == ''"
          :preIcon="IconEnum.DISABLE"
          @click="updateIndexStatus(0)"
          >禁用</a-button
        >
      </template>
      <template #bodyCell="{ text, column, record }">
        <template v-if="column.key === 'action'">
          <TableAction :actions="getAccountAction(handleView, record)" />
        </template>
        <template v-if="column.key === 'sort'">
          <a-input-number v-model:value="record[column.dataIndex]" />
        </template>
        <template v-if="column.key === 'code'">
          <span style="color: #338ed1" @click="toSqlConfig(record)">{{ text }}</span>
        </template>
        <template v-if="column.key === 'status'">
          <span style="color: red" v-if="text == 0">禁用</span>
          <span style="color: #30ca5a" v-if="text == 1">启用</span>
        </template>
      </template>
    </BasicTable>

    <!-- 新增弹窗 -->
    <Dialog
      v-model:visible="detailVisible"
      :title="detailTitle"
      @cancel="cancelDetail"
      :height="600"
      :width="800"
      smode="normal"
      overflowY="auto"
    >
      <a-form
        id="index-form"
        ref="indexFormRef"
        :model="formState"
        name="indexForm"
        labelAlign="center"
      >
        <a-row>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="指标类别"
              name="kind"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请选择指标类别' }]"
            >
              <DictSelectBox
                style="width: 100%"
                :disabled="isEdit"
                v-model:value="formState.kind"
                :type="`EvaluationIndexKind`"
              />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="指标名称"
              name="name"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请输入指标名称' }]"
            >
              <a-input :disabled="isEdit" v-model:value="formState.name" :maxlength="64" />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="评价方式"
              name="evaluationMethod"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请选择评价方式' }]"
            >
              <DictSelectBox
                style="width: 100%"
                :disabled="isEdit"
                v-model:value="formState.evaluationMethod"
                :type="`evaluation_method`"
              />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="考核目标"
              name="assessTarget"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请输入考核目标' }]"
            >
              <a-textarea
                :rows="4"
                v-model:value="formState.assessTarget"
                show-count
                :maxlength="128"
              />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="评分规则"
              name="scoreRuleDesc"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请输入评分规则' }]"
            >
              <a-textarea
                :rows="4"
                v-model:value="formState.scoreRuleDesc"
                show-count
                :maxlength="500"
              />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="是否可填附加分"
              name="allowOverrun"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请选择是否可填附加分' }]"
            >
              <DictSelectBox
                style="width: 100%"
                :disabled="isEdit"
                v-model:value="formState.allowOverrun"
                :type="`yes_or_no`"
              />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="指标修改人"
              name="updaterName"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
            >
              <a-input disabled v-model:value="formState.updaterName" />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="指标修改时间"
              name="updateTime"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
            >
              <a-input disabled v-model:value="formState.updateTime" />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="配置人"
              name="configurerName"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
            >
              <a-input disabled v-model:value="formState.configurerName" />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="配置时间"
              name="configTime"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
            >
              <a-input disabled v-model:value="formState.configTime" />
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
      <template #footer>
        <a-button style="margin-right: 10px" key="back" @click="cancelDetail">取消</a-button>
        <a-button key="submit" type="primary" @click="detailHandle">确定</a-button>
      </template>
    </Dialog>
    <!-- 规则配置弹窗 -->
    <Dialog
      v-model:visible="configVisible"
      title="评分规则"
      @cancel="cancelConfig"
      :height="600"
      :width="1000"
      smode="normal"
      overflowY="auto"
    >
      <a-form
        id="sql-form"
        ref="sqlFormRef"
        :model="sqlFormState"
        name="sqlForm"
        labelAlign="center"
      >
        <a-row :gutter="32">
          <a-col v-show="true" :span="24">
            <a-form-item
              label="指标名称"
              name="name"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
            >
              <a-input disabled v-model:value="sqlFormState.name" />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="指标编码"
              name="code"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请输入指标编码' }]"
            >
              <a-input :disabled="isShow" v-model:value="sqlFormState.code" :maxlength="32" />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="评分规则"
              name="scoreRuleDesc"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请输入评分规则' }]"
            >
              <a-textarea
                :rows="4"
                v-model:value="sqlFormState.scoreRuleDesc"
                show-count
                :disabled="isShow"
                :maxlength="500"
              />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="24">
            <a-form-item
              label="计算sql"
              name="calculationStatement"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
              :rules="[{ required: true, message: '请输入计算sql' }]"
            >
              <a-textarea
                :disabled="isShow"
                :rows="4"
                v-model:value="sqlFormState.calculationStatement"
                show-count
                :maxlength="1000"
              />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="12">
            <a-form-item
              label="配置人"
              name="configurerName"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
            >
              <a-input disabled v-model:value="sqlFormState.configurerName" />
            </a-form-item>
          </a-col>
          <a-col v-show="true" :span="12">
            <a-form-item
              label="配置时间"
              name="configTime"
              labelAlign="right"
              :labelCol="{ style: { width: '124px' } }"
            >
              <a-input disabled v-model:value="sqlFormState.configTime" />
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
      <template #footer>
        <a-button key="back" style="margin-right: 10px" @click="cancelConfig">取消</a-button>
        <a-button v-if="!isShow" key="submit" type="primary" @click="configHandle">确定</a-button>
      </template>
    </Dialog>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { onMounted, reactive, ref, nextTick } from 'vue';
  import { BasicTable, useTable, TableAction } from '/@/components/Table';
  import { PageWrapper } from '/@/components/Page';
  import { IconEnum } from '@/enums/appEnum';
  import { message } from 'ant-design-vue';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import PrintButton from '/@/components/Framework/Button/PrintButton.vue';
  import {
    columns,
    getDataList,
    saveIndex,
    searchFormSchema,
    saveSqlConfig,
    updateStatus,
    deleteIndex,
    saveSort,
  } from './index.ts';
  import { assign } from 'lodash-es';
  import { updateIndex } from '/@/views/baseset/operation/safecheck/safeCheckSet';
  import { useI18n } from '@/hooks/web/useI18n';
  import { useMessage } from '@/hooks/web/useMessage';
  import Dialog from '@/components/Framework/Modal/Dialog.vue';

  const { t } = useI18n();
  const { createConfirm } = useMessage();

  const searchInfo = reactive<Recordable>({}); // 查询信息
  const configVisible = ref<boolean>(false);
  const detailVisible = ref<boolean>(false);
  const formState = reactive({
    name: '',
    id: '',
    kind: '',
    evaluationMethod: '',
    assessTarget: '',
    scoreRuleDesc: '',
    allowOverrun: '',
  });
  const sqlFormState = reactive({});
  const detailTitle = ref('');
  const isEdit = ref<boolean>(false);
  const isShow = ref<boolean>(false);
  const indexFormRef = ref<FormInstance>();
  const sqlFormRef = ref<FormInstance>();
  let selectedRow = reactive({});
  let selectedRowId = ref('');

  const getDataListParam = async () => {
    let searchForm = getForm().getFieldsValue();
    searchForm.sortField = searchInfo.sortField;
    searchForm.sortOrder = searchInfo.sortOrder;
    let dataObj = await getDataList(searchForm);

    return dataObj;
  };

  const [registerTable, { reload, getForm, getDataSource }] = useTable({
    title: '评价指标',
    api: getDataListParam,
    rowKey: 'id',
    columns,
    useSearchForm: true,
    showTableSetting: true,
    bordered: true,
    pagination: { pageSize: 100 },
    handleSearchInfoFn(info) {
      return info;
    },
    formConfig: {
      labelWidth: 120,
      actionColOptions: { span: 24 },
      autoAdvancedLine: 1,
      baseColProps: { span: 7 },
      schemas: searchFormSchema,
      autoSubmitOnEnter: true,
      noHideBtnFlag: true,
    },
    rowSelection: {
      type: 'radio',
      fixed: true,
      onSelect: onSelect,
    },
    sortFn: (sortInfo: SorterResult) => {
      if (sortInfo.order) {
        searchInfo.sortField = sortInfo.field;
        searchInfo.sortOrder = sortInfo.order.replace('end', '');
      } else {
        searchInfo.sortField = '';
        searchInfo.sortOrder = '';
      }
    },
  });

  function handleSelectChange({ keys, rows }) {
    if (rows && rows.length > 0) {
      const record = rows[0];
      selectedRowId.value = record.id;
      selectedRow = record;
    } else {
      selectedRowId.value = '';
      selectedRow = null;
    }
  }

  function onSelect(record, selected) {
    if (selected) {
      selectedRowId.value = record.id;
      selectedRow = record;
    } else {
      selectedRowId.value = '';
      selectedRow = null;
    }
  }
  function handleEditEnd({ record, index, key, value }: Recordable) {
    saveIndex(record)
      .then(() => {
        message.success('已保存');
        reload();
      })
      .catch(() => {});
  }

  //---------------------------------操作按钮---------------------------------------
  // 查看指标sql配置
  const toSqlConfig = async (record: any) => {
    assign(sqlFormState, record);
    configVisible.value = true;
    isShow.value = true;
  };
  // 新增
  const handleAdd = async () => {
    detailTitle.value = '添加评价指标';
    detailVisible.value = true;
    indexFormRef.value?.resetFields();

    isEdit.value = false;
  };
  // 修改
  function handleEdit() {
    if (selectedRowId.value == '') {
      message.error('请先选择数据。');
      return;
    }
    detailTitle.value = '修改评价指标';
    nextTick(() => {
      assign(formState, selectedRow);
    });

    detailVisible.value = true;
    isEdit.value = true;
  }

  // 保存指标数据
  function detailHandle() {
    indexFormRef.value.validateFields().then(() => {
      // TODO 新增数据
      saveIndex(formState)
        .then(() => {
          message.success('保存成功');
          reload();
          //关闭弹窗

          indexFormRef.value?.resetFields();
          detailVisible.value = false;
        })
        .catch(() => {});
    });
  }

  // 关闭数据详情窗口
  function cancelDetail() {
    detailVisible.value = false;
  }

  // 配置sql
  function handleConfig() {
    if (selectedRowId.value == '') {
      message.error('请先选择数据。');
      return;
    }
    if (selectedRow.evaluationMethod != 'system') {
      message.warning('非系统评价，无须配置。');
      return;
    }
    assign(sqlFormState, selectedRow);
    configVisible.value = true;
    isShow.value = false;
  }

  // 关闭配置弹窗
  function cancelConfig() {
    configVisible.value = false;
  }
  // 保存配置
  function configHandle() {
    sqlFormRef.value.validateFields().then(() => {
      saveSqlConfig(sqlFormState)
        .then(() => {
          message.success('配置成功');
          sqlFormRef.value?.resetFields();
          configVisible.value = false;
          reload();
        })
        .catch(() => {});
    });
  }

  // 启用/禁用
  function updateIndexStatus(status: number) {
    let param = { id: selectedRowId.value, status: status };
    updateStatus(param).then(() => {
      reload();
    });
  }

  //删除
  function handleDel() {
    createConfirm({
      title: '提示',
      iconType: 'warning',
      content: '确定要删除选中记录？',
      async onOk() {
        deleteIndex(selectedRowId.value).then(() => {
          reload();
        });
      },
    });
  }

  // 保存排序号
  function saveSorts() {
    let indexList = getDataSource().map((item) => {
      return { id: item.id, sort: item.sort };
    });
    saveSort(indexList).then((data) => {
      message.success('已保存');
      reload();
    });
  }

  // 启动执行代码
  onMounted(async () => {});
</script>
<style lang="less" scoped>
  .fix-basic-table {
    :deep(&.vben-basic-table .ant-table-wrapper) {
      padding: 10px 16px 12px;
    }

    :deep(.table-settings .anticon-setting) {
      margin-right: 0;
    }
  }
</style>
