// 列表数据
export const getDataList = (params) =>
  defHttp.get(
    {
      url: Api.indexPage,
      params,
    },
    { isOnlyResult: true },
  );

// 保存
export const saveIndex = (params) =>
  defHttp.post(
    {
      url: Api.saveIndex,
      params,
    },
    {},
  );

// 修改
export const updateStatus = (params) =>
  defHttp.put(
    {
      url: Api.updateStatus,
      params,
    },
    {},
  );

// 删除
export const deleteIndex = (id) =>
  defHttp.delete(
    {
      url: Api.deleteIndex + '?id=' + id,
    },
    {},
  );

// 保存sql配置
export const saveSqlConfig = (params) =>
  defHttp.post(
    {
      url: Api.configSql,
      params,
    },
    {},
  );

// 保存排序号
export const saveSort = (params) =>
  defHttp.post(
    {
      url: Api.saveSort,
      params,
    },
    {},
  );
